import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import { createBrowserRouter, useSearchParams, RouterProvider } from "react-router-dom";
import { Chance } from 'chance'

const people = [
  'Andrew',
  'Jennie',
  'Stella',
  'Keira',
  'Maud',
  'Sandra',
  'Malin',
  'Kevin',
  'Ellie',
];

function App() {
  const router = createBrowserRouter([
    {
      path: "/maker",
      element: <Maker />,
    },
    {
      path: "/show",
      element: <Display />,
    },
    {
      path: "*",
      element: <Base />,
    }
  ]);

  return (
    <div className="App">
      <header className="App-header">
        Secret santa
      </header>
      <main className="App-main">
        <RouterProvider router={router} />
      </main>
    </div>
  );
}

const Maker = () => {
  const [searchParams, setSearchParams] = useSearchParams();

  const onNumberChange = (event) => {
    event.preventDefault();
    const numberString = event.target.value;
    setSearchParams({"number": numberString});
  };

  const number = searchParams.get("number") || 0;

  return <>
      <input type="text" onChange={onNumberChange} value={number}/>
      <SecretSantaList peopleList={people} number={number}/>
    </>;
};

const combineHash = (p, v) => {
  return ((p * 17) + v) % 65536;
};

const hashString = (str) => {
  var h = 0
  for (var i = 0; i < str.length; ++i) {
    h = combineHash(h, str.charCodeAt(i));
  }
  return h;
};

const shuffleList = (list, number) => {
  const chance = new Chance(...list, number);
  return chance.shuffle(list);
};

const getPersonMapping = (list, number) => {
  const sortedPeople = [...list];
  sortedPeople.sort();
  const giftOrder = shuffleList(sortedPeople, number);
  const mapping = {};
  mapping[giftOrder[giftOrder.length - 1]] = giftOrder[0];
  for (var i = 0; i < sortedPeople.length - 1; ++i) {
    mapping[giftOrder[i]] = giftOrder[i + 1];
  }
  console.log(list, number, sortedPeople);
  return mapping;
};

const SecretSantaList = ({
  peopleList,
  number
}) => {
  const mapping = getPersonMapping(peopleList, number);

  return (
    <ul>
      {
        Object.entries(mapping).map(([person, targetPerson]) => (
          <li key={person}>
            <LinkToDisplay
              person={person}
              targetPerson={targetPerson}
              number={number} />
          </li>
        ))
      }
    </ul>
  );
};

const LinkToDisplay = ({person, targetPerson, number}) => {
  const id = hashString(person);
  const href = `show?number=${number}&id=${id}`;
  return <a
    href={href}
    className='App-link'>
      {person}: {href}
  </a>;
};

const Display = () => {
  const [params, _] = useSearchParams();
  const number = params.get("number");
  const id = parseInt(params.get("id"));
  const mapping = getPersonMapping(people, number);
  const targetEntry = Object.entries(mapping).find(([person, targetPerson]) => hashString(person) === id);
  if (!targetEntry) {
    return <>Who are you?</>;
  }
  const [person, targetPerson] = targetEntry;
  return <>Hello {person} you should give to {targetPerson}</>;
};

const Base = () => {
  return <>Who's your secret santa? Who knows? ¯\_(ツ)_/¯</>
};

export default App;
